<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Registro de Venta
        </h1>
      </v-col>
      <v-col>
        <v-btn
          to="/adv/ventas"
          fab
          dark
          color="black"
          class="mx-5"
        >
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col class="mb-4">
        <v-stepper
          v-model="prueba"
          vertical
        >
          <form @submit.prevent="guardarVenta()">
            <v-stepper-step
              :complete="prueba > 1"
              step="1"
            >
              Ingresa un cliente
              <small>Ingresa el dni para búsqueda del cliente</small>
            </v-stepper-step>
            <v-stepper-content step="1">
              <v-card
                elevation="10"
                outlined
                shaped
                class="mb-12"
                height="300px"
              >
                <v-card-title>
                  <div
                    class="display-4 mb-2"
                    color="white"
                  >
                    Cliente
                  </div>
                </v-card-title>

                <v-card-text
                  class="mx-0 my-5"
                  align="center"
                >
                  <v-row>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-text-field
                        v-model="dni.customerDni"
                        label="DNI"
                        outlined
                        prepend-icon="mdi-barcode"
                        autofocus
                        required
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="4"
                    >
                      <v-btn
                        color="pink"
                        class="mr-4"
                        @click="buscarDni(dni)"
                      >
                        <v-icon>mdi-magnify</v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col>
                      <v-text-field
                        v-model="cliente.name"
                        label="Nombre Cliente"
                        outlined
                        prepend-icon="mdi-account"
                        disabled
                      />
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
              <v-btn
                color="pink"
                @click="prueba = 2"
              >
                Siguiente
              </v-btn>
              <v-btn
                text
                class="mx-5"
                to="/adv/ventas"
              >
                Cancelar
              </v-btn>
            </v-stepper-content>

            <v-stepper-step
              :complete="prueba > 2"
              step="2"
            >
              Ahora selecciona al Asesor de Ventas Senior
              <small>Selecciona dentro del combo el Asesor de la Venta</small>
            </v-stepper-step>

            <v-stepper-content step="2">
              <v-card
                elevation="10"
                outlined
                shaped
                class="mb-12"
                height="200px"
              >
                <v-card-title>
                  <div
                    class="display-4 mb-2"
                    color="white"
                  >
                    Vendedor
                  </div>
                </v-card-title>

                <v-card-text
                  class="mx-0 my-5"
                  align="center"
                >
                  <v-row>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-select
                        v-model="vendedorSelected"
                        label="Vendedor"
                        :items="listaVendedores"
                        item-text="name"
                        outlined
                        prepend-icon="mdi-account-cash"
                        required
                        autofocus
                        hide-selected
                        rounded
                      />
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
              <v-btn
                color="pink"
                @click="prueba = 3"
              >
                Siguiente
              </v-btn>
              <v-btn
                text
                class="mx-5"
                @click="prueba = 1"
              >
                Atrás
              </v-btn>
            </v-stepper-content>

            <v-stepper-step
              :complete="prueba > 3"
              step="3"
            >
              Selecciona el Tipo de Financiamiento
              <small>De seleccionar a crédito rellena los demás campos</small>
            </v-stepper-step>

            <v-stepper-content step="3">
              <v-card
                elevation="10"
                outlined
                shaped
                class="mb-12"
                height="200px"
              >
                <v-card-title>
                  <div
                    class="display-4 mb-2"
                    color="white"
                  >
                    Tipo de Financiamiento
                  </div>
                </v-card-title>
                <v-card-text
                  class="mx-0 my-5"
                  align="center"
                >
                  <v-row>
                    <v-col>
                      <v-select
                        v-model="tipoFinanciamiento"
                        label="TipoFinanciamiento"
                        :items="financiamiento"
                        item-text="type"
                        name="tipoFinanciamiento"
                        outlined
                        prepend-icon="mdi-account-bank"
                        required
                      />
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
              <v-btn
                color="pink"
                @click="prueba = 4"
              >
                Siguiente
              </v-btn>
              <v-btn
                text
                class="mx-5"
                @click="prueba = 2"
              >
                Atrás
              </v-btn>
            </v-stepper-content>

            <v-stepper-step
              :complete="prueba > 4"
              step="4"
            >
              Elige el Vehículo
              <small>Debes digitar la serie del vehículo correspondiente al vehículo
                asignado</small>
            </v-stepper-step>

            <v-stepper-content step="4">
              <v-card
                elevation="10"
                outlined
                shaped
                class="mb-12"
                height="300px"
              >
                <v-card-title>
                  <div
                    class="display-4 mb-2"
                    color="white"
                  >
                    Vehículo
                  </div>
                </v-card-title>
                <v-card-text
                  class="mx-0 my-5"
                  align="center"
                >
                  <v-row>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-text-field
                        v-model="carro.serie"
                        label="Serie TDP"
                        outlined
                        prepend-icon="mdi-barcode"
                        autofocus
                        required
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="4"
                    >
                      <v-btn
                        color="pink"
                        class="mr-4"
                        @click="buscarModelo(carro)"
                      >
                        <v-icon>mdi-magnify</v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col>
                      <v-text-field
                        v-model="vehiculo.model"
                        label="Modelo - Versión"
                        outlined
                        prepend-icon="mdi-car-sports"
                        disabled
                      />
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
              <v-btn
                color="pink"
                @click="prueba = 5"
              >
                Siguiente
              </v-btn>
              <v-btn
                text
                class="mx-5"
                @click="prueba = 3"
              >
                Atrás
              </v-btn>
            </v-stepper-content>

            <v-stepper-step
              :complete="prueba > 5"
              step="5"
            >
              Detalles de la Venta
              <small>Concluir el formulario ingresando la situación de la
                Venta</small>
            </v-stepper-step>

            <v-stepper-content step="5">
              <v-card
                elevation="10"
                outlined
                shaped
                class="mb-12"
                height="300px"
              >
                <v-card-title>
                  <div
                    class="display-4 mb-2"
                    color="white"
                  >
                    Detalle de Venta
                  </div>
                </v-card-title>
                <v-card-text
                  class="mx-0 my-5"
                  align="center"
                >
                  <v-row>
                    <v-col
                      cols="12"
                      md="3"
                    >
                      <v-text-field
                        v-model="sale.fecha"
                        label="Fecha de Venta"
                        outlined
                        prepend-icon="mdi-calendar"
                        autofocus
                        required
                        disabled
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-text-field
                        v-model="sale.nroComprobante"
                        label="Comprobante"
                        outlined
                        prepend-icon="mdi-barcode"
                        autofocus
                        required
                        disabled
                      />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col>
                      <v-select
                        v-model="sale.situacion"
                        label="Situación"
                        :items="opciones"
                        prepend-icon="mdi-list-status"
                        outlined
                        required
                        autofocus
                      />
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
              <v-btn
                color="pink"
                @click="prueba = 6"
              >
                Siguiente
              </v-btn>
              <v-btn
                text
                class="mx-5"
                @click="prueba = 4"
              >
                Atrás
              </v-btn>
            </v-stepper-content>

            <v-stepper-step
              :complete="prueba > 6"
              step="6"
            >
              Confirmar Registro de Venta
              <small>Darle a Guardar Venta</small>
            </v-stepper-step>
            <v-stepper-content step="6">
              <v-card
                elevation="10"
                outlined
                shaped
                class="mb-12"
                height="200px"
              >
                <v-card-title>
                  <div
                    class="display-4 mb-2"
                    color="white"
                  >
                    Confirmación del registro de Venta
                  </div>
                </v-card-title>
                <v-card-text
                  class="mx-0 my-5"
                  align="center"
                >
                  <v-row>
                    <v-card-actions>
                      <v-btn
                        to="/adv/ventas"
                        color="blue-grey"
                        class="mr-4"
                      >
                        Cancelar
                      </v-btn>
                      <v-btn
                        type="submit"
                        color="green"
                        class="mr-4"
                      >
                        Guardar
                      </v-btn>
                    </v-card-actions>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-stepper-content>
          </form>
        </v-stepper>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlBuscarDni = 'https://sistema-adv.herokuapp.com/api/customer/find'
  const urlGetVendedores = 'https://sistema-adv.herokuapp.com/api/seller/'
  const urlGetFinanciamiento = 'https://sistema-adv.herokuapp.com/api/financing/'
  const urlBuscarVehiculo = 'https://sistema-adv.herokuapp.com/api/vehicles/find/'
  const urlVenta = 'https://sistema-adv.herokuapp.com/api/sale/'
  var uuid = require('uuid')

  export default {
    name: 'EditarVenta',
    data () {
      return {
        prueba: 1,
        dni: {
          customerDni: '',
        },
        cliente: {
          name: '',
          document: '',
          cellphone: '',
          email: '',
          address: '',
        },
        vendedorSelected: {},
        listaVendedores: [],
        tipoFinanciamiento: {},
        financiamiento: [],
        carro: {
          serie: '',
        },
        vehiculo: {
          brand: '',
          cod_tdp: '',
          serie_tdp: '',
          category: '',
          model: '',
          colour: '',
          manufacturing_year: '',
          model_year: '',
          brand_office: '',
          location: '',
          price: '',
          status: '',
        },
        sale: {
          fecha: new Date().toISOString().substr(0, 10),
          nroComprobante: uuid.v4(),
          seller: '',
          vehicle: '',
          financing: '',
          entity: '',
          support: '',
          office: '',
          account_executive: '',
          situacion: '',
          customer: '',
        },
        opciones: ['Cancelado', 'Por Desembolsar', 'Libre', 'En Espera'],
      }
    },
    mounted () {
      this.obtenerVendedor()
      this.obtenerTipoFinanciamiento()
    },
    methods: {
      buscarDni (dni) {
        const variables = this.dni
        axios
          .post(urlBuscarDni, variables)
          .then((res) => {
            this.cliente = res.data
          })
          .catch((e) => {
            console.log(e)
          })
      },
      obtenerVendedor () {
        axios
          .get(urlGetVendedores)
          .then((res) => {
            this.listaVendedores = res.data
          })
          .catch((e) => console.log(e))
      },
      obtenerTipoFinanciamiento () {
        axios
          .get(urlGetFinanciamiento)
          .then((res) => {
            this.financiamiento = res.data
          })
          .catch((e) => {
            console.log(e)
          })
      },
      buscarModelo (carro) {
        const campos = this.carro
        axios
          .post(urlBuscarVehiculo, campos)
          .then((res) => {
            this.vehiculo = res.data
          })
          .catch((e) => {
            console.log(e)
          })
      },
      guardarVenta () {
        // Mandar campos al objeto sale
        this.sale.seller = this.vendedorSelected
        this.sale.customer = this.cliente.document
        this.sale.financing = this.tipoFinanciamiento
        this.sale.vehicle = this.vehiculo.serie_tdp

        const router = this.$router
        const params = this.sale

        // console.log(params);
        axios
          .post(urlVenta, params)
          .then((res) => {
            router.push('/adv/ventas')
          })
          .catch((e) => {
            console.log(e)
          })
      },
    },
  }
</script>
